.game-container {
  text-align: center;
  padding-top: 40px;
  font-family: "Poppins", sans-serif;
  background-color: #121213;
  color: #f5f5f5;
  height: 100vh;
}

.start-panel {
  margin-top: 30px;
}

.start-panel label {
  margin-right: 10px;
}

.start-panel select,
.guess-panel input {
  font-size: 1.2rem;
  padding: 6px 10px;
  border-radius: 6px;
  margin-right: 10px;
}


.start-panel button,
.guess-panel button {
  font-size: 1rem;
  padding: 6px 12px;
  border: none;
  border-radius: 6px;
  background-color: #6aaa64;
  color: white;
  cursor: pointer;
  transition: 0.3s;
  margin-top: 20px;
}

.start-panel button:hover,
.guess-panel button:hover {
  background-color: #538d4e;
}

.error {
  color: #ff6b6b;
  font-weight: bold;
  margin-top: 10px;
  font-size: 1rem;
  text-shadow: 0 0 6px rgba(255, 100, 100, 0.6);
}

.guess-panel {
  margin-top: 20px;
}

.banner {
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(50, 50, 50, 0.9);
  padding: 25px 50px;
  border-radius: 12px;
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
  animation: fadeInOut 3s ease-in-out;
  /*pointer-events: none;*/
}

.banner.win {
  background-color: rgba(106, 170, 100, 0.9);
}

.banner.lose {
  background-color: rgba(200, 80, 80, 0.9);
}

@keyframes fadeInOut {
  0% { opacity: 0; transform: translate(-50%, -60%); }
  20% { opacity: 1; transform: translate(-50%, -50%); }
  80% { opacity: 1; transform: translate(-50%, -50%); }
  100% { opacity: 0; transform: translate(-50%, -40%); }
}


.restart-btn {
  font-size: 1.1rem;
  padding: 10px 18px;
  border-radius: 8px;
  margin-top: 20px;
  background: linear-gradient(90deg, #4caf50, #2e7d32);
  color: white;
  font-weight: bold;
  transition: 0.3s;
  /*position: relative;*/
  /*z-index: 1; /* ensures itâ€™s under the banner visually */
  margin-top: 50px;
}

.restart-btn:hover {
  background: linear-gradient(90deg, #66bb6a, #388e3c);
  box-shadow: 0 0 8px #4caf50;
}